import React from 'react';
import ReactDOM from 'react-dom';
import singleSpaReact from 'single-spa-react';
import Root from './root.component';
import singleSpaCss from 'single-spa-css';

const cssLifecycles = singleSpaCss({
  // required: a list of CSS URLs to load
  // can be omitted if webpackExtractedCss is set to true, do not specify Webpack extracted css files here
  cssUrls: [],

  // optional: defaults to false. This controls whether extracted CSS files from Webpack
  // will automatically be loaded. This requires using the ExposeRuntimeCssAssetsPlugin,
  // which is documented below.
  webpackExtractedCss: true,
  shouldUnmount: true,
  timeout: 5000,
});

const lifecycles = singleSpaReact({
  React,
  ReactDOM,
  rootComponent: Root,
  errorBoundary(err, info, props) {
    return (
      <div className="h-16 flex items-center justify-between px-6 bg-primary text-white">Error</div>
    );
  },
});

class DiffSandbox {
  name: any;
  modifyMap: any;
  windowSnapshot: any;
  constructor(name: any) {
    this.name = name;
    this.modifyMap = {}; // Save modified attributes
    this.windowSnapshot = {};
  }

  active() {
    // Sandbox for caching active state
    this.windowSnapshot = {};
    for (const item in window) {
      this.windowSnapshot[item] = window[item];
    }
    console.log('this.windowSnapshot: ', this.windowSnapshot);
    Object.keys(this.modifyMap).forEach((p: any) => {
      window[p] = this.modifyMap[p];
    })
  }

  inactive() {
    for (const item in window) {
      if (this.windowSnapshot[item] !== window[item]) {
        // Record changes
        this.modifyMap[item] = window[item];
        // Restore window
        window[item] = this.windowSnapshot[item];
      }
    }
  }
}

const diffSandbox = new DiffSandbox('');

const activeSandbox = (props: any) => {
  console.log('activeSandbox props: ', props);
  diffSandbox.active();
  return Promise.resolve(props);
}

const inactiveSandbox = (props: any) => {
  console.log('inactiveSandbox props: ', props);
  diffSandbox.inactive();
  return Promise.resolve(props);
}

// Export an array of lifecycles to integrate with a framework's
// single-spa lifecycles. The order matters.
export const bootstrap = [cssLifecycles.bootstrap, lifecycles.bootstrap];

// The CSS lifecycles should be before your framework's mount lifecycle,
// to avoid a Flash of Unstyled Content (FOUC)
export const mount = [cssLifecycles.mount, activeSandbox, lifecycles.mount];

// The CSS lifecycles should be after your framework's unmount lifecycle,
// to avoid a Flash of Unstyled Content (FOUC)
export const unmount = [inactiveSandbox, lifecycles.unmount, cssLifecycles.unmount];
