// routes
import Router from './routes';
import { useNavigate } from "react-router-dom";
// theme
import { ThemeProvider } from 'pulse-portal-styleguide';
// components
import { NotistackProvider, ProgressBarStyle } from 'pulse-portal-styleguide';
import ScrollToTop from './components/ScrollToTop';
import MotionLazyContainer from './components/animate/MotionLazyContainer';
import { useEffect } from "react";
import { getToken } from 'pulse-portal-authentication';
import { PATH_HOME } from './routes/paths';
// redux
import { dispatch } from './redux/store';
import { setToken } from './redux/slices/main';
// ----------------------------------------------------------------------

export default function App() {
  const token = getToken();
  let navigate = useNavigate();
  useEffect(() => {
    if (token && token.length > 0) {
      dispatch(setToken(token));
    }
    else {
      return navigate("/");
    }
    
  }, [dispatch, token]);
  
  return (
    <MotionLazyContainer>
      <ThemeProvider>
        <NotistackProvider>
          <ProgressBarStyle />
          <ScrollToTop />
          <Router />
        </NotistackProvider>
      </ThemeProvider>
    </MotionLazyContainer>
  );
}
