import { Suspense, lazy, ElementType } from 'react';
import { Navigate, useRoutes, useLocation } from 'react-router-dom';
// redux
import { useSelector } from '../redux/store';
// layouts
import HomeLayout from '../layouts';
// components
import LoadingScreen from '../components/LoadingScreen';

// ----------------------------------------------------------------------

const Loadable = (Component: ElementType) => (props: any) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { pathname } = useLocation();

  return (
    <Suspense fallback={<LoadingScreen />}>
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  const { appName } = useSelector((state) => state.main);
  return useRoutes([
    {
      path: `/${appName}`,
      element: <HomeLayout appName={appName} />,
      children: [
        { element: <Navigate to={`/${appName}/list`} replace />, index: true },
        { path: 'list', element: <Roles /> },
        { path: 'list/:id/edit', element: <RoleCreate /> },
      ],
    },
    { path: `/${appName}/*`, element: <Navigate to="/404" replace /> },
  ]);
}

// Home
const Roles = Loadable(lazy(() => import('../pages/Roles')));
const RoleCreate = Loadable(lazy(() => import('../pages/RoleCreate')));
